nav, footer{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #0065cf;
    box-shadow: -2px 10px 3px -7px rgba(0,0,0,0.75);
    height: 16vh;
    color: rgb(215, 219, 255);
}

span{
    color: white;
    margin: 20px;
}

form{
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

hr{
    margin-top: 1.5rem;
    width: 80vw;
}

.search{
    align-self: center;
}

.search input{
    height: 2rem;
    width: 60vw;
    margin-right: 20px;
    padding: 0.5rem;
    font-size: 1rem;
}

.search button{
    height: 45px;
}


@media screen and (max-width:879px) {
    nav,footer{
        height: 8vh;
    }
}

@media screen and (max-width:512px) {
    nav,footer{
        height: 12vh;
    }
}