.containers{
    display: flex;
    gap: 16px;
    padding: 2rem;
    flex-wrap: wrap;
    min-height: 60vh;
}

.card{
    width: 400px;
    height: 400px;
    flex-grow: 1;
}

.card:last-child {
    flex-grow: 0;
}

.card img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    cursor: pointer;
}

@media screen and (max-width:879px) {
    .card{
        width: 280px;
        height: 500px;
        flex-grow: 1;
    }
}

@media screen and (max-width:512px) {
    .card{
        width: 300px;
        height: 400px;
        flex-grow: 1;
    }
}